import Image from "next/image";
import Link from "next/link";
import { NeonButton } from "@/components/ui/NeonButton";
import { BentoCard } from "@/components/ui/BentoCard";
import { BarChart3, Zap, Brain, Upload, Copy, FileText, CheckCircle2 } from "lucide-react";

export default function Home() {
  return (
    <div className="min-h-screen bg-[#050505] text-white selection:bg-brand-neon selection:text-black">
      {/* Navbar */}
      <nav className="fixed top-0 left-0 right-0 z-50 border-b border-white/5 bg-[#050505]/80 backdrop-blur-md">
        <div className="mx-auto flex h-16 max-w-7xl items-center justify-between px-6">
          <div className="flex items-center gap-2">
            <div className="size-8 rounded-lg bg-brand-neon flex items-center justify-center">
              <Zap className="text-black size-5 fill-current" />
            </div>
            <span className="text-xl font-bold tracking-tight">SeTutor</span>
          </div>

          <div className="hidden md:flex items-center gap-8 text-sm font-medium text-gray-400">
            <Link href="#features" className="hover:text-white transition-colors">Features</Link>
            <Link href="#how-it-works" className="hover:text-white transition-colors">How it works</Link>
            <Link href="#pricing" className="hover:text-white transition-colors">Pricing</Link>
          </div>

          <div className="flex items-center gap-4">
            <Link href="/login" className="text-sm font-medium hover:text-white text-gray-400 hidden sm:block">
              Log in
            </Link>
            <Link href="/signup">
              <NeonButton className="h-9 px-4 text-xs">Get Started</NeonButton>
            </Link>
          </div>
        </div>
      </nav>

      {/* Hero Section */}
      <section className="relative pt-32 pb-20 px-6">
        <div className="mx-auto max-w-7xl">
          <div className="grid lg:grid-cols-2 gap-12 items-center">

            {/* Left Content */}
            <div className="max-w-2xl">
              <div className="inline-flex items-center gap-2 rounded-full border border-white/10 bg-white/5 px-3 py-1 text-xs font-medium text-brand-neon mb-6">
                <span className="relative flex h-2 w-2">
                  <span className="animate-ping absolute inline-flex h-full w-full rounded-full bg-brand-neon opacity-75"></span>
                  <span className="relative inline-flex rounded-full h-2 w-2 bg-brand-neon"></span>
                </span>
                v2.0 is now live
              </div>

              <h1 className="text-5xl sm:text-7xl font-bold tracking-tight leading-[1.1] mb-6">
                Study costs,<br />
                <span className="text-gray-500">not sanity.</span>
              </h1>

              <p className="text-lg text-gray-400 mb-8 leading-relaxed max-w-md">
                Make learning automatic. Upload PDFs and let AI generate your flashcards, quizzes, and summaries instantly.
              </p>

              <div className="flex flex-wrap gap-4">
                <NeonButton className="h-12 px-8 text-base">Start Learning Free</NeonButton>
                <button className="h-12 px-8 rounded-full border border-white/10 bg-white/5 hover:bg-white/10 font-medium transition-colors">
                  View Demo
                </button>
              </div>

              <div className="mt-12 flex items-center gap-4 text-sm text-gray-500">
                <div className="flex -space-x-2">
                  {[1, 2, 3].map((i) => (
                    <div key={i} className="size-8 rounded-full bg-gray-800 border border-black" />
                  ))}
                </div>
                <p>Trusted by 10,000+ students</p>
              </div>
            </div>

            {/* Right Graphic (Dashboard Mockup) */}
            <div className="relative">
              <div className="relative z-10 rounded-xl border border-white/10 bg-[#0A0A0A] p-2 shadow-2xl">
                <div className="rounded-lg bg-[#111111] overflow-hidden">
                  {/* Mock UI Header */}
                  <div className="flex items-center gap-2 border-b border-white/5 px-4 py-3 bg-[#151515]">
                    <div className="flex gap-1.5">
                      <div className="size-3 rounded-full bg-red-500/20" />
                      <div className="size-3 rounded-full bg-yellow-500/20" />
                      <div className="size-3 rounded-full bg-green-500/20" />
                    </div>
                  </div>
                  {/* Mock UI Content */}
                  <div className="p-6 space-y-6">
                    <div className="flex justify-between items-end">
                      <div>
                        <p className="text-xs text-gray-500 uppercase font-bold tracking-wider mb-1">Total Flashcards</p>
                        <h3 className="text-3xl font-bold text-white">1,248</h3>
                      </div>
                      <div className="text-brand-neon text-sm font-medium">+24% this week</div>
                    </div>

                    {/* Graph mimic */}
                    <div className="h-32 flex items-end justify-between gap-2">
                      {[40, 70, 45, 90, 65, 85, 50].map((h, i) => (
                        <div key={i} className="w-full bg-brand-neon/20 rounded-t-sm hover:bg-brand-neon/40 transition-colors cursor-pointer relative group" style={{ height: `${h}%` }}>
                          <div className="absolute -top-8 left-1/2 -translate-x-1/2 bg-white text-black text-xs font-bold px-2 py-1 rounded opacity-0 group-hover:opacity-100 transition-opacity">
                            {h}
                          </div>
                        </div>
                      ))}
                    </div>
                  </div>
                </div>
              </div>

              {/* Decorative Glow */}
              <div className="absolute -top-20 -right-20 size-96 bg-brand-neon/20 blur-[100px] rounded-full pointer-events-none" />
            </div>
          </div>
        </div>
      </section>

      {/* Bento Grid Features */}
      <section id="features" className="py-24 px-6 bg-[#0A0A0A] border-y border-white/5">
        <div className="mx-auto max-w-7xl">
          <h2 className="text-3xl font-bold mb-12 max-w-xl">
            Everything you need to <span className="text-brand-neon">ace your exams</span> without the burnout.
          </h2>

          <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
            {/* Large Card 1 */}
            <BentoCard
              className="md:col-span-2 min-h-[300px]"
              title="Upload once, generate everything."
              description="Drop your PDF lecture notes and watch SeTutor generate flashcards, summaries, and quizzes in seconds."
              icon={<Upload className="size-6" />}
            >
              <div className="mt-8 flex gap-4 overflow-hidden mask-linear-fade">
                <div className="flex-1 bg-[#111] rounded-lg p-4 border border-white/5">
                  <div className="h-2 w-20 bg-gray-800 rounded mb-4" />
                  <div className="space-y-2">
                    <div className="h-2 w-full bg-gray-800 rounded opacity-50" />
                    <div className="h-2 w-3/4 bg-gray-800 rounded opacity-50" />
                  </div>
                </div>
                <div className="flex-1 bg-[#111] rounded-lg p-4 border border-brand-neon/20">
                  <div className="flex items-center gap-2 mb-4">
                    <div className="size-4 bg-brand-neon rounded-full" />
                    <div className="h-2 w-12 bg-white/20 rounded" />
                  </div>
                  <div className="space-y-2">
                    <div className="h-2 w-full bg-white/10 rounded" />
                    <div className="h-2 w-5/6 bg-white/10 rounded" />
                    <div className="h-2 w-4/6 bg-white/10 rounded" />
                  </div>
                </div>
              </div>
            </BentoCard>

            {/* Tall Card 2 */}
            <BentoCard
              className="md:row-span-2 bg-brand-neon text-black"
              title="Smart Flashcards"
              description="Spaced repetition built-in to help you remember longer."
              icon={<Brain className="size-6 text-black" />}
            >
              <div className="bg-black/10 rounded-2xl p-6 mt-8 h-full flex items-center justify-center relative overflow-hidden">
                <div className="absolute inset-0 flex items-center justify-center">
                  <div className="w-32 h-40 bg-white shadow-xl rounded-xl rotate-6 absolute top-10 right-4" />
                  <div className="w-32 h-40 bg-[#F5F5F3] shadow-lg rounded-xl -rotate-6 absolute top-14 left-4" />
                  <div className="w-32 h-40 bg-white border-2 border-black rounded-xl z-10 flex items-center justify-center font-bold text-center p-4">
                    What is the powerhouse of the cell?
                  </div>
                </div>
              </div>
            </BentoCard>

            {/* Card 3 */}
            <BentoCard
              title="Instant Summaries"
              description="Get the gist of 100-page documents in 30 seconds."
              icon={<FileText className="size-6" />}
            >
              <div className="mt-4 space-y-2">
                {[1, 2, 3].map(i => (
                  <div key={i} className="flex gap-2 items-center text-sm text-gray-400">
                    <CheckCircle2 className="size-4 text-brand-neon" />
                    <span>Key concept {i} extracted</span>
                  </div>
                ))}
              </div>
            </BentoCard>

            {/* Card 4 */}
            <BentoCard
              title="Plan Your Study"
              description="AI creates a personalized schedule."
              icon={<Copy className="size-6" />}
            />
          </div>
        </div>
      </section>

      {/* Dark Section */}
      <section className="py-24 px-6 bg-black">
        <div className="mx-auto max-w-4xl text-center">
          <h2 className="text-4xl md:text-5xl font-bold mb-6">We've got your back.</h2>
          <p className="text-xl text-gray-400 mb-12">
            Join thousands of students who switched to smarter learning.
          </p>

          <div className="grid grid-cols-2 md:grid-cols-4 gap-4 text-left">
            {[
              { label: "Documents Processed", value: "10k+" },
              { label: "Hours Saved", value: "50k+" },
              { label: "Flashcards Created", value: "1M+" },
              { label: "Exam Success Rate", value: "94%" },
            ].map((stat, i) => (
              <div key={i} className="p-6 rounded-2xl bg-[#0F0F0F] border border-white/5">
                <div className="text-3xl font-bold text-white mb-2">{stat.value}</div>
                <div className="text-sm text-gray-500">{stat.label}</div>
              </div>
            ))}
          </div>
        </div>
      </section>

      {/* Footer */}
      <footer className="py-12 px-6 border-t border-white/5 bg-[#050505] text-sm text-gray-500">
        <div className="mx-auto max-w-7xl flex flex-col md:flex-row justify-between items-center gap-6">
          <div className="flex items-center gap-2">
            <div className="size-6 rounded bg-brand-neon flex items-center justify-center">
              <Zap className="text-black size-3 fill-current" />
            </div>
            <span className="text-white font-bold">SeTutor</span>
          </div>

          <div className="flex gap-8">
            <a href="#" className="hover:text-white transition-colors">Privacy</a>
            <a href="#" className="hover:text-white transition-colors">Terms</a>
            <a href="#" className="hover:text-white transition-colors">Twitter</a>
          </div>

          <p>Â© 2025 SeTutor Inc.</p>
        </div>
      </footer>
    </div>
  );
}
