import React from "react";
import { cn } from "@/lib/utils";

interface NeonButtonProps extends React.ButtonHTMLAttributes<HTMLButtonElement> {
    children: React.ReactNode;
    variant?: "primary" | "secondary" | "outline";
    fullWidth?: boolean;
}

export const NeonButton = React.forwardRef<HTMLButtonElement, NeonButtonProps>(
    ({ className, children, variant = "primary", fullWidth, ...props }, ref) => {
        const variants = {
            primary: "bg-brand-neon text-brand-black hover:bg-[#b3e600] border-transparent",
            secondary: "bg-brand-grey text-white hover:bg-brand-dark-grey border-transparent",
            outline: "bg-transparent border-white/20 text-white hover:border-brand-neon hover:text-brand-neon",
        };

        return (
            <button
                ref={ref}
                className={cn(
                    "inline-flex items-center justify-center rounded-full px-6 py-3 text-sm font-semibold transition-all duration-200 active:scale-95",
                    "border",
                    variants[variant],
                    fullWidth && "w-full",
                    className
                )}
                {...props}
            >
                {children}
            </button>
        );
    }
);

NeonButton.displayName = "NeonButton";
